.c-button {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    padding: 20px;
    border: 1px solid #ED0000;
    background: #ED0000;
    border-radius: 2px;

    &-transparent {
        color: #222;
        border: 1px solid #333639;
        background: #fff;
    }

    &-red {
        display: block;
        width: max-content;
        color: #ED0000;
        padding: 20px 20px 20px 50px;
        border: 1px solid #ED0000;
        background: #fff url('../svg/red-arrow.svg') center left 20px no-repeat;
    }
}