.breadcrumb {
    display: flex;
    padding: 34px 0;
    margin-bottom: 15px;
    background: none;

    & > div,
    a {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: bold;
      font-size: 10px;
      line-height: 14px;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: #7E7E7E;
    }

    a::after {
      content: '/';
      padding: 0 5px;
    }
}
