.advantages {
    margin: 45px 0 80px;

    .col-lg-3 {
        @media (max-width: 992px) {
            margin-bottom: 30px;
        }
    }

    &__item {
        height: 100%;
        padding: 23px 30px;
        border: 2px solid #EFEFEF;
        box-sizing: border-box;
        border-radius: 2px;
    }

    .heading-3 {
        font-weight: 600;
        font-size: 18px;
        line-height: 1;
        color: #ED0000;
        margin: 15px 0;
    }

    .text {
        font-size: 14px;
        line-height: 24px;
        color: #222222;
        margin-bottom: 0;
    }
}