.l-single {

    .heading-1 {
        margin-bottom: 35px;
    }

    .product {
        .heading-3 {
            font-size: 25px;
            margin-bottom: 15px;
            
            @media (max-width: 768px) {
                margin-bottom: 0; 
            } 
        }

        .fotorama {
            @media (min-width: 768px) {
                height: 100%;
            }

            &__wrap {
                @media (min-width: 768px) {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    width: 100% !important;
                    height: 100%;
                }
            }

            &__nav-wrap, 
            &__nav {
                @media (min-width: 768px) {
                    width: 100px !important;
                    height: 100%; 
                }               
            }

            &__nav__frame--thumb.fotorama__active {
                @media (min-width: 768px) {
                    width: calc(100% - 36px)!important;
                }

                img {
                    @media (min-width: 768px) {
                        left: 3px !important;
                        width: calc(100% - 3px)!important;
                        border: 2px solid red!important;   
                    }            
                }
            }

            &__nav__shaft {
                @media (min-width: 768px) {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-orient: vertical;
                    -webkit-box-direction: normal;
                    -ms-flex-direction: column;
                    flex-direction: column;
                }               
            }
            
            &__thumb {
                @media (min-width: 768px) {
                    left: -20px;
                    width: calc(100% + 20px);
                    height: 100%; 
                }              

              &-border {
                @media (min-width: 768px) {
                    border: none;
                    background: none;
                }                
              }

              img {
                @media (min-width: 768px) {
                    left: 0 !important;
                }                
              }
            }

            &__stage, 
            &__stage__shaft,
            &__stage__frame img {
                @media (min-width: 768px) {
                    height: 100% !important; 
                }              
            }

            &__stage__frame img {
                @media (min-width: 768px) {
                    -o-object-fit: cover;
                    object-fit: cover;
                    -o-object-position: center;
                    object-position: center; 
                }              
            }
        }          

        .category {
            display: block;
            font-family: 'Nunito Sans', sans-serif;
            font-weight: 600;
            font-size: 13px;
            line-height: 1;
            text-decoration-line: underline;
            color: #B9B9B9;
            margin-bottom: 30px;

            @media (max-width: 768px) {
                margin-top: 30px;
            }
        }

        .contact {
            font-family: 'Nunito Sans', sans-serif;
            font-weight: 800;
            font-size: 13px;
            line-height: 20px;
            color: #ED0000;
            padding: 20px 10px 20px 15px;
            background: #EDEDED;
            border-radius: 3px;

            & > div {
                margin-top: 20px;

                @media (max-width: 400px) {
                    flex-wrap: wrap;
                    justify-content: flex-start;
                } 
            }

            span {
                color: #333639;
            }

            a {
                font-family: 'Nunito Sans', sans-serif;
                font-weight: normal;
                font-size: 13px;                
                text-decoration-line: underline;
                color: #ED0000;
            }

            .phone::before,
            .arrow::after {
                content: '';
                display: inline-block;
                width: 12px;
                height: 12px;
            }

            .phone::before {
                margin-right: 10px;
                background: url('../svg/phone-red.svg') center left no-repeat;
            }

            .arrow::after {
                height: 6px;
                margin-left: 10px;
                background: url('../svg/arrow-right-red.svg') center right no-repeat;
            }
        }

        .price {
            font-family: 'Nunito Sans', sans-serif;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            margin: 25px 0 20px;

            del {                
                font-weight: bold;
                font-size: 13px;
                line-height: 18px;                
                color: #B9B9B9;
            }

            span {
                font-weight: 900;
                font-size: 27px;
                line-height: 37px;                
                color: #333639;
            }
        }

        .order {
            @media (max-width: 576px) {
                flex-wrap: wrap;
            }

            button:nth-of-type(1) {
                @media (max-width: 576px) {
                    margin-bottom: 15px;
                }                
            }

            .c-button {
                min-width: calc(50% - 15px);
                white-space: nowrap;
                padding: 10px 20px;
                margin-top: 0;            
            }
        }

        .c-button {            
            white-space: nowrap;
            margin-top: 40px;
        }

        .offer {
            font-family: 'Nunito Sans', sans-serif;
            font-weight: 600;
            font-size: 12px;
            line-height: 60px;
            color: #B9B9B9;
            margin-top: 20px;

            @media (max-width: 576px) {
                line-height: 20px;
            } 

            span {
                font-size: 13px;
                color: #333639;
                margin-left: 5px;
            }

            .views {
                @media (max-width: 576px) {
                    width: 100%;
                    text-align: right;
                } 

                &::before {
                    content: '';
                    display: inline-block;
                    width: 15px;
                    height: 9px;
                    margin: 0 10px 0 40px;
                    background: url('../svg/views.svg') center no-repeat;
                }
            }
        }
    }

    .link-other {
        font-family: 'Nunito Sans', sans-serif;
        font-weight: 900;
        font-size: 20px;
        line-height: 60px;
        text-decoration-line: underline;
        color: #B9B9B9;
        white-space: nowrap;
        margin-left: 68px;

        @media (max-width: 768px) {
            margin-left: 0; 
        } 
    }

    .data {
        padding-bottom: 7px;   
        margin: 0 0 10px;
        background: #EDEDED;
        border-radius: 3px;

        &.data-full {
            margin: 0 15px 20px;
            padding: 0;
        }

        &.data-full > div:nth-of-type(3),
        &.data-full > div:nth-of-type(7),
        &.data-full > div:nth-of-type(11),
        &.data-full > div:nth-of-type(15),
        &.data-full > div:nth-of-type(4n) {
            @media (min-width: 992px) {
                background: #F4F4F4; 
            }                 
        }

        &.data-full > div:nth-of-type(odd) {
            padding-left: 100px;

            @media (max-width: 991px) {
                padding-left: 15px;
            }  
        }

        &.data-full > div:nth-of-type(even) {
            padding-right: 100px;

            @media (max-width: 991px) {
                padding-right: 15px;
            }  
        }

        &.data-full > div:nth-of-type(2n) {
            @media (max-width: 991px) {
                background: #F4F4F4; 
            }  
        }

        &-name {
            align-self: flex-end;
            font-family: 'Nunito Sans', sans-serif;
            font-size: 16px;
            line-height: 43px;
            color: #333639;
            white-space: nowrap;
            padding-right: 10px;

            @media (max-width: 460px) {
                line-height: 1;
                white-space: normal;
                padding: 10px;
            }
        }

        &.data-full .data-name {
            line-height: 68px;

            @media (max-width: 460px) {
                line-height: 1;
            }
        }

        &-value {
            min-width: 170px;            
            padding: 0 0 0 10px;

            @media (max-width: 460px) {
                min-width: auto;
                padding: 10px;                
            }
        }

        &.data-full .data-value {
            font-weight: 800;
        }

        &-dots {
            width: 100%;
            height: 30px;
            border-bottom: 2px dotted #B9B9B9;

            @media (max-width: 460px) {
                height: calc(100% - 12px);
            }
        }

        &.data-full .data-dots {
            height: 40px;
        }
    }
}