.heading-1,
.heading-2,
.heading-3 {
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 900;
    font-size: 34px;
    line-height: 60px;
    letter-spacing: -0.01em;
    margin-bottom: 30px;
    color: #333639;
}

.heading-2 {
    @media (max-width: 768px) {
      font-size: 30px;
      line-height: 40px;
    }
}

.heading-1 {
    font-size: 50px;

    @media (max-width: 768px) {
      font-size: 40px;
      line-height: 40px;
    }
}

.heading-3 {
    @media (max-width: 576px) {
        font-size: 24px;
    }
}

.text {
    font-family: 'Nunito Sans', sans-serif;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: #333639;
    margin-bottom: 30px;
}