.l-catalog {
  .mb-75 {
    margin-bottom: 75px;

    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
  }

  .sidebar {
    background: #333639;
    border-radius: 3px;
    margin-bottom: 30px;

    @media (max-width: 1200px) {
      display: none;        

      &.open {
        display: block;
        position: absolute;
        left: 15px;
        z-index: 10;
      }
    }

    &__title {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 900;
      font-size: 20px;
      line-height: 60px;
      color: #FFE81F;
      padding: 13px 20px;
      background: #2D3033;
      border-radius: 3px 3px 0 0;
    }

    ul {
      list-style: none;
      padding-left: 0;
    }

    &__list {
      padding: 10px 0;
      transition: .3s;

      a {
        display: block;
        font-family: 'Nunito Sans', sans-serif;
        font-weight: bold;
        font-size: 14px;
        letter-spacing: -0.01em;
        color: #fff;
        padding: 10px 20px;

        &:active,
        &:hover,
        &:focus {
          background: #ED0000;
          text-decoration: none;
        }

        span {
          display: block;
          font-size: 12px;
          letter-spacing: 0.02em;
          color: #9EA5AC;
        }
      }

      ul {
        display: none;
        background: #2D3033;
      }

      li.active ul {
        display: block;

        a {
          padding: 10px 20px 10px 40px;
        }
      }
    }
  }

  .filter {
    @media (max-width: 1200px) {
      display: none;
      transition: .3s;

      &.open {
        display: block;
        position: absolute;
        left: 15px;
        z-index: 10;
        padding: 15px 15px 15px 0;
        background: #fff;
      }
    }

    &__item {
      padding: 26px 20px;
      margin-bottom: 10px;
      border: 2px solid #EFEFEF;
      box-sizing: border-box;
      border-radius: 2px;      
    }

    & > div:nth-last-of-type(1) {
      margin-top: 20px;
    }

    &__title {
      width: 100%;
      position: relative;
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 800;
      font-size: 13px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #222;

      &::after {
        content: '';
        position: absolute;
        top: 2px;
        right: 0;
        width: 15px;
        height: 15px;
        background: url('../svg/filter-icon.svg') center no-repeat;
        transition: .3s;
        transform: rotate(180deg);
      }

      &.open {
        margin-bottom: 20px;
      }

      &.open::after {
        transform: rotate(0deg);
      }
    }

    &__body {
      display: none;
      transition: .3s;

      &.open {
        display: flex;
      }

      input[type=checkbox] {
        position: absolute;
        z-index: -1;
        opacity: 0;
      }

      label {
        position: relative;
        display: block;
        width: 100%;
        font-family: 'Nunito Sans', sans-serif;
        font-size: 14px;
        line-height: 36px;
        letter-spacing: 0.02em;
        color: #5D636B;
        padding-left: 30px;
        margin-bottom: 0;

        &::before {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 18px;
          height: 18px;
          background: #EBEBEB;
          border: 6px solid #EBEBEB;
          border-radius: 1px;
        }
      }

      input:checked + label {
        color: #ED0000;
      }

      input:checked + label::before {
        background: #ED0000;
      }
    }

    &__range {
      position: relative;
      width: 98%;
      -webkit-appearance: none;
      height: 1px;
      margin-bottom: 16px;
      background: #ED0000;
      outline: none;
      
      &::before,
      &::after {
        content: '';
        position: absolute;
        top: -5px;
        left: 0;
        width: 1px;
        height: 10px;
        background: #ED0000;
      }

      &::after {
        left: auto;
        right: 0;
      }
    }

    &__range::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 12px;
      height: 12px;
      background: #fff;
      border: 4px solid #ED0000;
      transform: rotate(45deg);
      cursor: pointer;
    }
    
    &__range::-moz-range-thumb {
      width: 12px;
      height: 12px;
      background: #fff;
      border: 4px solid #ED0000;
      transform: rotate(45deg);
      cursor: pointer;
    }

    &__number {
      max-width: 98%;

      input {
        max-width: 50%;
        font-family: 'Nunito Sans', sans-serif;
        font-size: 13px;
        line-height: 20px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: #424242;
        padding: 16px 20px 20px 20px;
        background: #F4F4F4;
        border: none;
        border-radius: 3px;

        &:nth-of-type(1) {
          max-width: calc(50% - 20px);
        }
      }
    }

    button {
      width: calc(50% - 4px);
    }
  }

  .results {
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 700;
    font-size: 15px;
    color: #ED0000;

    @media (max-width: 1200px) {
      display: flex;
      justify-content: space-between;
    }

    @media (max-width: 576px) {
      justify-content: flex-start;
    }

    span {
      color: #333639;
    }

    div {
      @media (max-width: 1200px) {
        display: none;
      }
    }

    button {
      display: none;
      min-width: 48%;
      padding: 19px;
      background: #f6f6f6;
      border: none;
      border-radius: 3px;

      @media (max-width: 768px) {
        background: none;
        padding: 19px 0;
        text-align: left;
      }

      @media (max-width: 576px) {
        min-width: 0;
        margin-right: 20px;
      }

      svg {
        fill: #C8C8C8;
      }

      span {
        margin-left: 10px;

        @media (max-width: 450px) {
          display: none;
        }
      }

      &.active svg {
        fill: #222;
      }

      @media (max-width: 1200px) {
        display: block;
      }
    }
  }

  .sort {
    @media (max-width: 768px) {
      order: 1;
      margin-bottom: 20px;
    }

    & > div {
      width: 100%;
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 60px;
      color: #b8b8b8;
      background: #f6f6f6;
      border-radius: 3px;

      & > div:nth-of-type(1) {
        padding: 0 15px;
      }
    }

    select {
      appearance: none;
      -webkit-appearance: none;
      height: 100%;
      padding: 0 14px;
      margin-bottom: 0;
      background: #eee;
      border: none;
      border-radius: 3px;
      color: #333639;
      text-decoration: underline;
    }
  }

  .layout {

    @media (max-width: 992px) {
      margin-top: 20px;
    }

    @media (max-width: 768px) {
      margin: 0;
    }

    button {
      background: none;
      border: none;

      svg {
        fill: #C8C8C8;
      }

      &.active svg {
        fill: #222;
      }
    }
  }

  .products {
    margin-top: 48px;

    @media (max-width: 768px) {
      order: 2;
      margin-top: 0;
    }

    .slide_item {
      margin-bottom: 30px;

      @media (max-width: 1200px) {
        width: calc(33.3% - 15px);
      }

      @media (max-width: 992px) {
        width: calc(50% - 15px);
      }

      @media (max-width: 768px) {
        width: 100%;
      }
    }

    .product_box {
      @media (max-width: 1200px) {
        width: 100%;
      }

      .option_box {
        @media (max-width: 768px) {
          justify-content: flex-end;
        }
      }

      .pr_more {
        @media (max-width: 768px) {
          margin-right: 10px;
        }
      }
    }

    .product_box .product_info .cost_box p {
      color: #222;
      margin: 10px 0 32px;
    }

    .product_box .product_info .cost_box del + p {
      color: #ED0000;
      margin: 0 0 20px;
    }

    &.layout-list {
      .slide_item {
        width: 100%;
      }

      .product_box {
        @media (min-width: 500px) {
          width: 100%;
          display: flex;
        }

        .product_image {
          @media (min-width: 500px) {
            min-height: 100%;
            height: auto;
          }
        }
      }
    }
  }

  .slick_product-arrow {
    position: relative;         

    &.slick-slider .slick-track, 
    &.slick-slider .slick-list {
        display: flex;  
        margin: 0 -30px 0 0px;
        
        @media (max-width: 768px) {
          margin: 0;
        }
    }

    .product_box {
      @media (min-width: 768px) and (max-width: 992px) {
        width: calc(100% - 30px);
      }
    }

    .slick-arrow{
        font-size: 0;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        z-index: 1;
        display: inline-block;
        padding: 0;
        width: 14px;
        height: 14px;
        border: solid 2px #B9B9B9;
        border-right: none;
        border-bottom: none;
        border-radius: 2px;
        background: none;
        box-shadow: none;
        outline: none;
        &:hover{
            border-color: #ED0000;
        }

    }

    .slick-prev{
        left: -30px;
        transform: rotate(-45deg);
    }

    .slick-next{
        right: -30px;
        transform: rotate(135deg);
    }

    .slick-dots{
      list-style: none;
      padding-left: 0;
      margin-left: 20px;
      margin-right: 20px;
      text-align: center;
      margin-bottom: 0;
      margin-top: 10px;
      .slick-active{
          button{
              border: 1px solid #ED0000;
          }
      }
      li{
          display: inline-block;
          font-size: 0;
          margin: 0 13px;
          button{
              padding: 0;
              width: 5px;
              height: 5px;
              border-radius: 100%;
              background-color: rgb(219, 219, 219);
              border: 1px solid #C8C8C8;
              border:none;
              outline: none;

          }
      }
    }
  }


  .pagination {
    margin: 70px auto 80px;

    @media (max-width: 768px) {
      order: 3;
      margin: 30px auto 40px;
    }

    ul {
      list-style: none;
      width: 100%;
    }

    li {
      padding: 15px 30px;

      @media (max-width: 768px) {
        order: 3;
        padding: 15px;
      }

      @media (max-width: 576px) {
        order: 3;
        padding: 10px;
      }
    }

    a {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: bold;
      font-size: 14px;
      line-height: 30px;
      text-align: center;
      letter-spacing: 0.02em;
      text-decoration: underline;
      color: #25292E;
    }

    .active a {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: -12px;
        left: -18px;
        width: 45px;
        height: 45px;
        padding-top: 7px;
        border: 1px solid #ED0000;
        border-radius: 50%;
        box-sizing: border-box;

        @media (max-width: 768px) {
          top: -4px;
          left: -10px;
          width: 30px;
          height: 30px;
        }

        @media (max-width: 576px) {
          top: -2px;
          left: -7px;
          width: 25px;
          height: 25px;
        }
      }
    }

    .prev,
    .next {
      display: block;
      width: 12px;
      height: 100%;
      background: url('../svg/pag-arrow.svg') center no-repeat;
    }

    .next {
      transform: rotate(180deg);
    }
  }
}
