.question {
    display: flex;
    margin: 62px auto 47px;

    @media (max-width: 768px) {
      margin-top: 32px;
    }

    .heading-2 {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 900;
      font-size: 45px;
      line-height: 62px;
      letter-spacing: 0.03em;
      text-transform: uppercase;
      color: #ED0000;
      margin-bottom: 36px;

      @media (max-width: 576px) {
        font-size: 30px;
        line-height: 40px;
      }
    }

    p.text {
      max-width: 290px;
    }

    form {
      & > div:nth-last-of-type(1) {
        @media (max-width: 576px) {
          flex-wrap: wrap;
        }
      }

      .d-flex {
        width: 100%;
      }
    }

    &__item {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: bold;
      font-size: 10px;
      line-height: 14px;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      padding: 26px 10px 20px 57px;
      margin-bottom: 20px;
      background: #fff;
      border: none;
      border-radius: 3px;

      &-name {
        width: calc(50% - 15px);
        margin-right: 15px;
        background: #fff url(../svg/user.svg) center left 24px no-repeat;

        @media (max-width: 576px) {
          width: 100%;
          margin-right: 0;
        }
      }

      &-phone {
        width: calc(50% - 15px);
        margin-left: 15px;
        background: #fff url(../svg/phone-call.svg) center left 24px no-repeat;

        @media (max-width: 576px) {
          width: 100%;
          margin-left: 0;
        }
      }

      &-text {
        background: #fff url(../svg/dots-form.svg) center left 24px no-repeat;
      }
    }

    textarea {
      resize: none;
    }

    ::placeholder,
    :-moz-placeholder {
      color: #7E7E7E;
    }

    &__agree {
      @media (min-width: 576px) {
        order: 2;
      }

      input[type=checkbox] {
        position: absolute;
        z-index: -1;
        opacity: 0;
      }

      label {
        position: relative;
        display: block;
        width: 100%;
        max-width: max-content;
        font-family: 'Nunito Sans', sans-serif;
        font-weight: bold;
        font-size: 10px;
        line-height: 14px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: #333639;
        padding: 3px 30px;
        margin: 0 auto;

        @media (max-width: 576px) {
          margin: 10px 0;
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 20px;
          height: 20px;
          background: #fff;
          border-radius: 1px;
        }
      }

      input:checked + label {
        color: #333639;
      }

      input:checked + label::before {
        background: #fff url('../svg/checking-mark.svg') center no-repeat;
      }
    }

    .submit {
      position: static;
      max-width: max-content;
    }
  }