.container{
    min-width: 1200px;
}

body{
    margin: 0;
    padding: 0;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

h1{
    font-size: 34px;
    letter-spacing: -0.01em;
    font-family: $mbl;
    color: #333639;
}

// Прелоадер
@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(2);
                transform: scale(2);
    }
    75% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(2);
                transform: scale(2);
    }
    75% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

.spinner {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: #ffb100;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
}

.spinner > .dot {
    position: absolute;
    height: 3rem;
    width: 3em;
}

.spinner > .dot:after {
    content: '';
    display: block;
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    background: #f40303;
}

.spinner > .dot:nth-of-type(1) {
    -webkit-animation: rotate 1s linear infinite;
            animation: rotate 1s linear infinite;
    -webkit-animation-delay: 0;
            animation-delay: 0;
}

.spinner > .dot:nth-of-type(1):after {
    -webkit-animation: pulse 3s linear infinite;
            animation: pulse 3s linear infinite;
}

.spinner > .dot:nth-of-type(2) {
    -webkit-animation: rotate 1.1s linear infinite;
            animation: rotate 1.1s linear infinite;
    -webkit-animation-delay: .1s;
            animation-delay: .1s;
}

.spinner > .dot:nth-of-type(2):after {
    -webkit-animation: pulse 4s linear infinite;
            animation: pulse 4s linear infinite;
}

.spinner > .dot:nth-of-type(3) {
    -webkit-animation: rotate 1.2s linear infinite;
            animation: rotate 1.2s linear infinite;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

.spinner > .dot:nth-of-type(3):after {
    -webkit-animation: pulse 5s linear infinite;
            animation: pulse 5s linear infinite;
}

.spinner > .dot:nth-of-type(4) {
    -webkit-animation: rotate 1.3s linear infinite;
            animation: rotate 1.3s linear infinite;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}

.spinner > .dot:nth-of-type(5) {
    -webkit-animation: rotate 1.4s linear infinite;
            animation: rotate 1.4s linear infinite;
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}

.spinner > .dot:nth-of-type(6) {
    -webkit-animation: rotate 1.5s linear infinite;
            animation: rotate 1.5s linear infinite;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

.spinner > .dot:nth-of-type(7) {
    -webkit-animation: rotate 1.7s linear infinite;
            animation: rotate 1.7s linear infinite;
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
}

// --- Продукт для слайдер ( Глобальный стиль ) ---
.slick_product{
    position: relative;
    .slick-arrow{
        font-size: 0;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        z-index: 1;
        display: inline-block;
        padding: 0;
        width: 14px;
        height: 14px;
        border: solid 2px #B9B9B9;
        border-right: none;
        border-bottom: none;
        border-radius: 2px;
        background: none;
        box-shadow: none;
        outline: none;
        &:hover{
            border-color: #ED0000;
        }

    }
    .slick-prev{
        left: -30px;
        transform: rotate(-45deg);
    }
    .slick-next{
        right: -30px;
        transform: rotate(135deg);
    }
    .slide_item{
        margin: 0 15px;
        outline: none;
    }

    .slick-dots{
        list-style: none;
        padding-left: 0;
        margin-left: 20px;
        margin-right: 20px;
        text-align: center;
        margin-bottom: 0;
        margin-top: 10px;
        .slick-active{
            button{
                border: 1px solid #ED0000;
            }
        }
        li{
            display: inline-block;
            font-size: 0;
            margin: 0 13px;
            button{
                padding: 0;
                width: 5px;
                height: 5px;
                border-radius: 100%;
                background-color: rgb(219, 219, 219);
                border: 1px solid #C8C8C8;
                border:none;
                outline: none;

            }
        }
    }
}
.product_box{
    width: 270px;
    display: inline-block;
    background: #F4F4F4;
    border-radius: 3px;
    .product_image{
        width: 100%;
        height: 186px;
        background-position: center center!important;
        background-size: cover!important;
        background-repeat: no-repeat!important;
        padding: 15px;
        p{
            font-family: $msb;
            font-size: 11px;
            line-height: 15px;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            padding: 10px 12px;
            display: inline-block;
        }
        .pr_day{
            color: #FE1818;
            background-color: #fff;
            border-radius: 2px;
        }
        .pr_new{
            color: #209E71;
            background-color: #fff;
            border-radius: 2px;
        }
        .pr_stock{
            color: #fff;
            border-radius: 3px;
            background: linear-gradient(90deg, #FE1818 -13.04%, #F87045 107.61%);
        }
    }
    .product_info{
        padding: 20px 20px 22px 22px;
        h4{
            font-family: $mr;
            font-size: 18px;
            line-height: 28px;
            color: #222222;
            margin-bottom: 16px;
            width: 100%;
        }
        .cost_box{
            display: inline-block;
            width:50%;
            float: left;
            del{
                font-family: $msb;
                line-height: 18px;
                letter-spacing: 0.05em;
                text-transform: uppercase;
                color: #222222;
            }
            p{
                font-family: $msb;
                font-size: 21px;
                letter-spacing: 0.05em;
                text-transform: uppercase;
                color: #ED0000;

            }
        }
        .option_box{
            display: flex;
            justify-content: space-between;
            float: right;
            width: 50%;
            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                height: 50px;
                border-radius: 2px;
                transition: 0.2s;
                outline: none;
            }
            .pr_more{
                background-color: #fff;
                &:hover{
                    border: 1px solid #FF2D2D;
                }
            }
            .pr_cart{
                background-color: #ED0000;
                &:hover{
                    background-color: #FF2D2D;
                }
            }
        }
    }

}

// header
.header{
    padding-top: 23px;
    padding-bottom: 18px;
    background-color: #fff;
    z-index: 9999;

    .logo_box{
        max-width: 170px;
        img{
