    .location_box{
        text-align: center;
        margin: 5px 0;
        a{
            font-family: $msb;
            font-size: 9px;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: #333639;
            background: #F6F6F6;
            border-radius: 2px;
