            }
            img{
                margin-right: 12px;
                max-width: 12px;
                margin-top: 0px;
            }
        }
    }

    .shares_box{
        float: right;
        a{
            font-family: $msb;
            font-size: 9px;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #222222;
            text-decoration: none;
            background: #FFE81F;
            border-radius: 2px;
            padding: 9px 10px;
            display: inline-block;
            transition: 0.2s;
            &:hover{
                background: #e4d01a;
            }
        }
    }

    .close_nav{
        display: none;
    }

    .navbar{
        padding: 0;
        margin: 5px 0;
    }

    .navbar-nav{
        display: inline-block;
        width: 100%;
        li.active{
            background: #F6F6F6;
            border-radius: 2px;
            a{
                color: #ED0000;
                text-decoration: none;
            }
        }
        li{
            display: inline-block;
            a{
                font-family: $msb;
                font-size: 9px;
                letter-spacing: 0.08em;
                text-transform: uppercase;
                color: #333639;
                display: inline-block;
                padding: 9px 7px;
            }
        }
    }

    .call_box{
        max-width: 175px;
        float: right;
        display: inline-block;
        a{
            font-family: $msb;
            font-size: 14px;
            color: #222222;
            margin: 7px 0;
            margin-top: -4px;
            display: block;
            white-space: nowrap;
            text-decoration: none;
            position: relative;
            padding-left: 27px;
            &:hover{
                span{
                    text-decoration: underline;
                }
            }
            img{
                position: absolute;
                left: 0;
                top: 3px;
            }
        }
    }

    // responsive display none
    .location_box.res,
    .shares_box.res,
    .call_box.res{
        display: none;
    }
    // ---


}

// header_bottom
.header_bottom{
    background: #ED0000;
    height: 80px;
    position: relative;
    position: sticky;
    z-index: 999;
    top: -1px;
    left: 0;
    ::-webkit-scrollbar {
        width: 10px;
      }

      /* Track */
      ::-webkit-scrollbar-track {
        background: #f1f1f1;
      }

      /* Handle */
      ::-webkit-scrollbar-thumb {
        background: #888;
      }

      /* Handle on hover */
      ::-webkit-scrollbar-thumb:hover {
        background: #555;
      }
    .catalog_button_hovered{
        background: #cf1111!important;
        img{
            transform: rotate(90deg);
        }
    }

    .catalog_button.zIndex{
        z-index: 997;
    }

    .catalog_button{
        background: #FF1111;
        display: inline-block;
        box-shadow: none;
        font-family: $msb;
        font-size: 17px;
        line-height: 21px;
        letter-spacing: 0.04em;
        color: #FFFFFF;
        max-width: 170px;
        width: 100%;
        position: relative;
        border-radius: 0px;
        padding-left: 53px;
        padding-top: 18px;
        padding-bottom: 18px;
        padding-right: 14px;
        transition: 0.2s;
        height: 80px;
        z-index: 999;
        &:hover{
            background: #cf1111;
            img{
                transform: rotate(90deg);
            }
        }
        img{
            position: absolute;
            top: 0;
            left: 25px;
            bottom: 0;
            margin: auto;
            transition: 0.2s;
        }
    }
    .form_search{
        display: inline-block;
        width: 100%;
        position: relative;
        margin: 10px 0;
        input{
            width: 100%;
            height: 60px;
            border:none;
            background: #FFFFFF;
            border-radius: 2px;
            outline: none;
            padding-left: 38px;
            padding-right: 70px;
            font-family: $mr;
        }
        .submit{
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            width: 61px;
            height: 100%;
            border-radius: 0px;
            &:hover{
                background-color: rgb(241, 241, 241);
            }
        }
    }
    .close_catalog_nav{
      display: none;
    }
    .order_options{
        display: flex;
        width: 100%;
        max-width: 170px;
        height: 100%;
        float: right;
    }
    .order_call{
        font-family: $meb;
        font-size: 10px;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FFFFFF;
        background: #FF1111;
        display: flex;
        line-height: 20px;
        align-items: center;
        justify-content: center;
        padding: 14px 8px;
        max-width: 90px;
        text-decoration: none;
        transition: 0.2s;
        &:hover{
            background: #cf1111;
        }
    }
    .shop_cart{
        background: #333639;
        display: flex;
        align-items: center;
        max-width: 80px;
        width: 100%;
        justify-content: center;
        position: relative;
        transition: 0.2s;
        &:hover{
            background: #202224;
        }
        img{
            margin-left: -10px;
        }
        span{
            width: 15px;
            height: 15px;
            border-radius: 100%;
            position: absolute;
            font-family: $msb;
            font-size: 9px;
            line-height: 12px;
            letter-spacing: 0.08em;
            color: #FFFFFF;
            background: #FF1111;
            justify-content: center;
            display: flex;
            top: -28px;
            right: -29px;
            bottom: 0;
            left: 0;
            margin: auto;
            align-items: center;
        }
    }
    .search_button_mobile{
        display: none;
    }

    .catalog_overlay{
        position: fixed;
        background: #000;
        opacity: 0.5;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 998;
        display: none;
    }

    .catalog_box{
        margin-top: 80px;
        position: absolute;
        top: 0;
        left: 0;
        max-height: 82vh;
        margin-bottom: 80px;
        overflow-x: hidden;
        display: inline-block;
        background: #fff;
        width: 100%;
        display: none;
        z-index: 998;
        &:before{
            content:"";
            position: absolute;
            top: 0;
            left: -1200px;
            right: 0;
            margin: auto;
            max-width: 800px;
            width: 100%;
            background: #F4F4F4;
            height: 100%;
            z-index: -1;
        }
        &:hover{
            display: inline-block!important;
        }

    }

    .catalog_nav{
        padding-left: 0;
        list-style: none;
        width: 100%;
        padding-top: 30px;
        padding-bottom: 30px;
        margin-bottom: 0;
        position: relative;
        &:before{
            content:"";
            position: absolute;
            top: 0;
            left: -1200px;
            right: 0;
            margin: auto;
            max-width: 800px;
            width: 100%;
            background: #F4F4F4;
            height: 100%;
            z-index: -1;
        }
        h4{
            margin: 10px 0 20px;
            font-family: $mbl;
            font-size: 22px;
            letter-spacing: -0.01em;
            color: #333639;

        }
        h5{
            font-size: 19px;
            letter-spacing: -0.01em;
            margin: 10px 0 20px;
            color: #ED0000;
            font-family: $mbl;
            padding-left: 30px;
        }
        li{
            list-style: none;
            display: block;
            padding: 15px 0;
            cursor: auto;
            font-family: $msb;
            font-size: 15px;
            .nav_menu{
                width: 100%;
                display: inline-block;
                max-width: 370px;
                position: relative;
                &::after{
                    content:"";
                    position: absolute;
                    top: 0;
                    right: -20px;
                    bottom: 0;
                    margin: auto;
                    width: 9px;
                    height: 15px;
                    background-image: url(../svg/arrow-right.svg);
                    background-size: 9px 15px;
                    background-repeat: no-repeat;
                    opacity: 0;
                }
                &:before{
                    content: "";
                    position: absolute;
                    top: -15px;
                    bottom: -15px;
                    width: 100%;
                    z-index: -1;
                    padding: 26px;
                    width: 830px;
                    right: -50px;
                    transition: 0.2s;
                }
            }
            &:hover{
                font-family: $meb;
                .nav_menu{
                    &::after{
                        opacity: 1;
                    }
                    &:before{
                        content: "";
                        background: #FFE81F;
                    }
                }
                ul{
                    display: inline-block;
                    opacity: 1;
                }
            }
            ul{
                left: 406px;
                opacity: 0;
                display: none;
                position: absolute;
                top: 0;
                height: 100%;
                max-width: 385px;
                width: 100%;
                max-height: 783px;
                overflow-x: hidden;
                margin-left: 30px;
                padding-top: 30px;
                transition: 0.2s;
                li{
                    padding-left: 30px;
                    padding-right: 30px;
                    color:#000;
                    a{
                        text-decoration: none;
                        color:#000;
                    }
                    &:hover{
                        background: #F4F4F4;
                    }
                }
            }
        }
    }
}

