            right: 20px;
            font-size: 30px;
            background: none;
            box-shadow: none;
            color:#fff;
            box-shadow: none;
            border:none;
        }
        .navbar-collapse {
            display: block;
            position: fixed;
            width: 360px;
            max-width: 85%;
            height: 100%;
            top: 0;
            left: 0;
            bottom: 0;
            z-index: 9999;
            overflow: auto;
            visibility: hidden;
            opacity: 0;
            padding: 45px 20px;
            margin: 0;
            -webkit-overflow-scrolling: touch;
            background: #152050;
            transform: translateX(-100%);
            transition: all 0.5s ease;

        }
        .navbar-collapse.open{
            visibility: visible;
            opacity: 1;
            transform: translateX(0);
            box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.75);
        }

        .call_box{
