            width: 30%;
            a{
                width: 100%;
            }
        }

        .navbar-toggler{
            flex-shrink: 0;
            margin-left: auto;
            width: 40px;
            height: 40px;
            padding: 8px;
            display: inline-block;
            line-height: 0;
            border-radius: 100%;
            box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.068);
            transition: 0.2s;
            text-align: left;
            outline: none;
            &:hover, &:focus{
                box-shadow:inset 0px 0px 15px rgba(0, 0, 0, 0.068);
            }
            span{
                display: inline-block;
                width: 100%;
                height: 3px;
                border-radius: 3px;
                background-color: #ed0000;
                top: -2px;
                position: relative;

                &:nth-child(2){
                    width: 80%;
                }
                &:nth-child(3){
                    width: 60%;
                }
            }
        }
        .navbar-nav{
            li{
                display: block;
                text-align: left;
                a{
                    color:#fff;
                    display: inline-block;
                    font-size: 10px;
                }
